<template>
	<div id="promoters">
		<commonHeader :title="'成为推广员'"></commonHeader>
		<div class="header">
			<div class="header-content" v-html="article">
				如果没法简洁的表达你的想法，那只说明你还不够了解它。如果没法简洁的表达你的想法，那只说明你还不够了解它。
			</div>
		</div>
		<div class="content">
			<input type="checkbox" v-model="checked"/><span>我已阅读并同意此协议</span>
		</div>
		<div class="btn" @click="next">下一步</div>
	</div>
</template>

<script>
	
export default {
	data(){
		return {
			checked:true,
			article: null
		}
	},
  components: {
  },
  created(){
  	this.getQuerySetting();
  },
  methods:{
  	getQuerySetting(){
  		this.$Api.getQuerySetting('推广员协议').then((res) => {
  			if(res.q.s == 0){
    				this.getArticleDetails(res.q.settings.value);
  			}
  		})
  	},
  	getArticleDetails(id){
  		this.$Api.getArticleDetails(id).then((res) => {
  			if(res.q.s == 0){
				this.article = res.q.article.content
  			}
  		})
  	},	
  	next(){
  		if(this.checked){
  			this.$router.push({name:'PromoteInfo'})
  		}else{
  			this.$toast('请先同意推广协议')
  		}
  	}
  }
  
}
</script>

<style lang="scss">
	@import "../../../../sass/baseSass.scss";
	#promoters{
		.header{
			padding: 0.4rem;
			border-radius: 0.11rem;
			font-size: 0.4rem;
			color: #333;
			.header-content{
				padding: 0.4rem;
				box-shadow: 0px -5px 9px 0px rgba(67, 67, 67, 0.06), 0px 5px 10px 0px rgba(0, 0, 0, 0.13);
			}
		}
		.content{
			display: flex;
			padding: 0 0.4rem;
			align-items: center;
			input[type=checkbox]{
				position: relative;
				width: 0.53rem;
				height: 0.53rem;
				-webkit-appearance: none;
				&::after{
					content: '';
			    position: absolute;
			    top: 0;
			    left: 0;
			    width: 0.53rem;
			    height: 0.53rem;
			    background: #fff;
			    border: 1px solid #eee;
			    border-radius: 50%;
			    z-index: 9;
			    -webkit-box-sizing: border-box;
			    box-sizing: border-box;
				}
				&:checked {
	        &::after {
	          border: none;
	          background: url("../../../../assets/cart/ico_checked.png") no-repeat center;
	          background-size: 100%;
	        }
	      }
			}
			>span{
				font-size: 0.37rem;
				margin-left: 0.27rem;
				color: #666;
			}
		}
		.btn{
			width: 9.2rem;
			height: 1.2rem;
			margin: 0.91rem auto 0 auto;
			line-height: 1.2rem;
			border-radius: 0.6rem;
			text-align: center;
			color: #fff;
			@include bg-color;
		}
	}
</style>


